<template>
  <div class="trip-preview" @click="showDetails">
    <pre>{{ first3partic[3]}}</pre>
    <img class="trip-image" :src="trip.imgUrl" />

    <div>
      <h3 class="title">{{trip.title}}</h3>
      <h5 class="text-grey">{{trip.start}}-{{trip.end}}</h5>
    </div>
    <div v-if="first3partic" class="avatars">
      <template >
        <v-avatar v-if="first3partic[0]" class="participants participant-0" size="42px" color="yellow">
          <img :src=" first3partic[0].imgUrl" alt="alt" />
        </v-avatar>

        <v-avatar v-if="first3partic[1]" class="participants participant-1" size="42px" color="yellow">
          <img :src=" first3partic[1].imgUrl" alt="alt" />
        </v-avatar>

        <v-avatar v-if="first3partic[2]" class="participants participant-2" size="42px" color="yellow">
          <img :src=" first3partic[2].imgUrl" alt="alt" />
        </v-avatar>

        <div v-if="moreParticipants ">
          <span class="moreParticipants">{{moreParticipants}} more</span>
        </div>

        <div >
         owner {{owner.imgUrl}}
          <v-avatar class="ownerAvatar" size="54px" color="white">
            <img :src="owner.imgUrl" />
          </v-avatar>
        </div>
      </template>
    </div>

    <div class="likes">
      <span
        v-show="trip.likedBy.length>0"
        class="like-counter"
      >{{trip.likedBy.length}} @click="like"</span>
      <i class="icon icon-love-it-circle"></i>
    </div>
    <!-- <div v-for="(type,idx) in trip.types" :key="idx">
      this.tripType=trip.types[idx]
      <i class="icon" :class="classObject"></i>
    </div> -->
  </div>
</template>



<script>
export default {
  props: {
    trip: Object
  },
  data() {
    return {
      participantsList: [],
      owner: [],
      moreParticipants: 0,
      tripType: "",
      tripIconMap: {
        "Day Trip": "day-sunrise-2",
        Spirtual: "religion-islam-pray",
        Art: "color-painting-palette",
        Historical: "history-vase",
        Cycling: "",
        Camping: "camping-tent-2",
        Hiking: "trekking-person",
        Beach: "beach-parasol-water",
        Culinary: "asian-food-rice"
      }
    };
  },
  methods: {
    showDetails() {
      this.$router.push("/TripDetails/" + this.trip._id);
    }
  },

  async created() {
    try {
      await this.$store.dispatch({
        type: "getUsers",
        tripId: {tripId: this.trip._id }
      });

      this.participantsList = this.$store.getters.participantsList;
    } catch (err) {
      console.log(err);
    }

    console.log("this.participantsList", this.$store.getters.participantsList);

    // if (participantsList && participantsList.length > 4)
    //   this.moreParticipants = this.participantsList.length - 3;

    try {
      this.owner = await this.$store.dispatch({
        type: "getUserById",
        id: this.trip.owner._id 
      });

      console.log(" this.owner", this.owner);
    } catch (err) {
      console.log(err);
    }
  },
  like() {
   // TODO dispach add to likes (logic that owener can't add and user my add only once?)
  },
  computed: {
    first3partic() {
      return this.participantsList.slice(0, 3);
    },

    classObject: function() {
      return {
        "day-sunrise-2": this.tripType === "Day Trip",
        "religion-islam-pray": this.tripType === "Spirtual",
        "color-painting-palette": this.tripType === "Art",
        "history-vase": this.tripType === "Historical",
        "camping-tent-2": this.tripType === "Camping",
        "trekking-person": this.tripType === "Hiking",
        "beach-parasol-water": this.tripType === "Beach",
        "asian-food-rice": this.tripType === "Culinary"
      };
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../styles/components/_TripPreview.scss";
</style>
