<template>
  <section v-if="!theLoggedInUser" class="login-container">
    <h2>Login</h2>
    <form @submit.prevent="login" class="flex column">
      <input type="email" placeholder="Enter Email" minlength="3" v-model="user.email" required autofocus>
      <input type="password" placeholder="Enter Password" minlength="2" v-model="user.pass" required>
      <button>Login</button>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      user: {email: '', pass: ''},
      loggedInUser: null
    };
  },
  computed:{
    theLoggedInUser(){
      return this.$store.getters.loggedInUser;
    }
  },
  methods:{
    async login(){
      // loading.io
      try{
        await this.$store.dispatch({type: 'login', user:this.user})
          this.user = {email: '', pass: ''}
      }catch(err){
        console.log(err);
        // swal
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/views/_Login.scss";
</style>
