<template>
  <section class="home">
    <div class="header">
      <AppHeader dark />
      <Login v-if="loginModal" />
      <TripFilter class="filter" @setFilter="setFilter" />
    </div>
    <router-link to="/TripEdit">New Trip</router-link>
    <section class="main-container">
      <h2>My Trips</h2>
      
      <div class="my-trips" v-if="ownerTrips.length && loggedInUser">
        <!-- <Carousel :trips="ownerTrips"></Carousel> -->
        <div v-for="(trip,idx) in trips" :index="idx" :key="idx">
          <TripPreview :trip="trip" />
        </div>

        
      </div>
      
      <div class="all-trips">
        <h2>All Trips</h2>
        <div v-for="(trip,idx) in trips" :index="idx" :key="idx">
          <TripPreview :trip="trip" />
        </div>
      </div>
    </section>
  </section>
</template>


<script>
import TripFilter from "../components/TripFilter";
import TripPreview from "../components/TripPreview";
import AppHeader from "../components/Header";
import Login from "../components/Login";
// import Carousel from "../components/Carousel";

export default {
  data() {
    return {
      ownerTrips: []
    };
  },
  created() {
    this.setFilter();
  },
  computed: {
    trips() {
      return this.$store.getters.trips;
    },
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    loginModal() {
      return this.$store.getters.loginModal;
    }
  },
  methods: {
    async setFilter(filter = {}) {
      const tripsToShow = await this.$store.dispatch({
        type: "loadTrips",
        filter
      });
      if (this.loggedInUser)
        this.ownerTrips = tripsToShow.filter(
          trip => trip.owner._id === this.loggedInUser._id
        );
    }
  },
  components: {
    TripFilter,
    AppHeader,
    Login,
    // Carousel,
    TripPreview
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/views/_Home.scss";
</style>